<template>
  <article :class="$style.container">
    <projects-grid/>
  </article>
</template>

<script>
  import ProjectsGrid from '@/components/ProjectsGrid';

  export default {
    name: 'Projects',

    components: {
      ProjectsGrid,
    }
  }
</script>

<style module>
  .container {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
  }
</style>



