<template>
  <ul :class="$style.container">
    <li>
      <letter-head/>
    </li>
    <router-link :to="`/${p.slug}`" tag="li" v-for="(p, i) in pages"
      :key="i"
    >
      <a>{{ p.title }}</a>
    </router-link>
  </ul>
</template>

<script>
  import { createNamespacedHelpers } from 'vuex';
  const { mapState, mapActions } = createNamespacedHelpers('pages');

  import LetterHead from '@/components/CompanyLetterhead';

  export default {
    components: {
      LetterHead,
    },

    computed: {
      ...mapState([
        'pages'
      ]),
    },

    methods: {
      ...mapActions({
        fetchPages: 'fetch'
      })
    },

    created(){
      this.fetchPages();
    }

  };
</script>

<style module>
  .container {
    display: grid;
    grid-auto-flow: column;
    grid-auto-columns: max-content;
    grid-template-rows: 1fr;
    grid-gap: 3em;
  }
</style>



